// frontend/src/components/layout/ThemeToggle.tsx (Version garantie)
'use client';

import { Sun, Moon } from 'lucide-react';
import { useTheme } from '@/app/providers/AnimationProvider';

export default function ThemeToggle() {
  const { theme, toggleTheme } = useTheme();

  return (
    <button
      onClick={toggleTheme}
      className="relative w-12 h-12 flex items-center justify-center rounded-full 
                 bg-gradient-to-br from-gray-100 to-white 
                 dark:from-gray-800 dark:to-gray-900
                 shadow-lg hover:shadow-xl
                 border border-gray-200 dark:border-gray-700
                 transition-all duration-300
                 hover:scale-105 active:scale-95
                 group"
      aria-label={`Basculer en mode ${theme === 'dark' ? 'clair' : 'sombre'}`}
    >
      {/* Icone */}
      <div className="relative z-10 transform transition-transform duration-300 group-hover:scale-110">
        {theme === 'dark' ? (
          <Sun className="w-5 h-5 text-yellow-500 animate-spin-slow" />
        ) : (
          <Moon className="w-5 h-5 text-gray-700 dark:text-gray-300" />
        )}
      </div>

      {/* Effet de halo */}
      <div className="absolute inset-0 rounded-full border-2 border-primary-300/20 
                     dark:border-primary-500/10 animate-pulse" />

      {/* Tooltip */}
      <div className="absolute -top-10 left-1/2 transform -translate-x-1/2 
                      px-3 py-1.5 bg-gray-900 dark:bg-gray-800 text-white text-xs 
                      rounded-lg whitespace-nowrap opacity-0 group-hover:opacity-100 
                      transition-opacity duration-200 pointer-events-none">
        Mode {theme === 'dark' ? 'clair' : 'sombre'}
        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-1/2 
                        w-2 h-2 bg-gray-900 dark:bg-gray-800 rotate-45" />
      </div>
    </button>
  );
}